import React, { useState } from "react";
import { Container, Row, Col, Card, Form, Button } from "react-bootstrap";
import { FaSearch, FaCalendarAlt, FaTags, FaArrowRight } from "react-icons/fa";
import * as Constants from "../../Constants/Constants";
import "./Blog.css";

const Blog = ({ navigateTo }) => {
  // Import blog posts from Constants.js
  const blogPosts = Constants.BLOG_POSTS;

  // State for filtering and searching
  const [searchTerm, setSearchTerm] = useState("");
  const [categoryFilter, setCategoryFilter] = useState("All");

  // Get unique categories for filter options
  const categories = ["All", ...new Set(blogPosts.map(post => post.category))];

  // Filter blog posts based on search and category
  const filteredPosts = blogPosts.filter(post => {
    const matchesSearch = 
      post.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
      post.excerpt.toLowerCase().includes(searchTerm.toLowerCase()) ||
      post.tags.some(tag => tag.toLowerCase().includes(searchTerm.toLowerCase()));
    
    const matchesCategory = categoryFilter === "All" || post.category === categoryFilter;
    
    return matchesSearch && matchesCategory;
  });

  return (
    <div className="blog-page">
      <div className="page-header">
        <Container>
          <h1>Blog</h1>
          <p>
            Thoughts, insights, and knowledge sharing on engineering leadership,
            quality systems, and technology.
          </p>
        </Container>
      </div>

      <Container className="blog-container">
        {/* Search and Filter Bar */}
        <div className="search-filter-section">
          <Row>
            <Col lg={8} md={7}>
              <div className="search-box">
                <FaSearch className="search-icon" />
                <Form.Control
                  type="text"
                  placeholder="Search articles..."
                  value={searchTerm}
                  onChange={(e) => setSearchTerm(e.target.value)}
                />
              </div>
            </Col>
            <Col lg={4} md={5}>
              <Form.Group>
              <Form.Control as="select" value={categoryFilter} onChange={(e) => setCategoryFilter(e.target.value)}>
  {categories.map((category, index) => (
    <option key={index} value={category}>
      {category}
    </option>
  ))}
</Form.Control>

              </Form.Group>
            </Col>
          </Row>
        </div>

        {/* Blog Post Grid */}
        <div className="blog-grid">
          <Row>
            {filteredPosts.length === 0 ? (
              <Col xs={12} className="text-center no-results">
                <h3>No articles found matching your search.</h3>
                <p>Try different search terms or categories.</p>
              </Col>
            ) : (
              filteredPosts.map((post, index) => (
                <Col lg={6} md={6} className="blog-post-col" key={index}>
                  <Card className="blog-post-card">
                    <div className="blog-post-image-container">
                      <Card.Img
                        variant="top"
                        src={post.image}
                        className="blog-post-image"
                      />
                      <div className="blog-post-category">
                        <span>{post.category}</span>
                      </div>
                    </div>
                    <Card.Body>
                      <div className="blog-post-meta">
                        <span className="blog-post-date">
                          <FaCalendarAlt className="icon" /> {post.date}
                        </span>
                        <span className="blog-post-read-time">{post.readTime}</span>
                      </div>
                      <Card.Title>{post.title}</Card.Title>
                      <Card.Text>{post.excerpt}</Card.Text>
                      <div className="blog-post-tags">
                        <FaTags className="icon" />
                        {post.tags.map((tag, i) => (
                          <span key={i} className="blog-post-tag">
                            {tag}
                          </span>
                        ))}
                      </div>
                      <button
                        onClick={() => navigateTo('blog', { blog: post.id })}
                        className="read-more-link"
                      >
                        Read Article <FaArrowRight className="icon" />
                      </button>
                    </Card.Body>
                  </Card>
                </Col>
              ))
            )}
          </Row>
        </div>

        {/* Newsletter Signup Section */}
        <div className="newsletter-section">
          <Row className="align-items-center">
            <Col lg={6} md={6}>
              <div className="newsletter-content">
                <h2>Subscribe to My Newsletter</h2>
                <p>
                  Get notified when I publish new articles on engineering leadership,
                  quality systems, and technology. No spam, unsubscribe anytime.
                </p>
              </div>
            </Col>
            <Col lg={6} md={6}>
              <div className="newsletter-form">
                <Form className="d-flex">
                  <Form.Control
                    type="email"
                    placeholder="Your email address"
                    className="newsletter-input"
                  />
                  <Button variant="primary" className="newsletter-button">
                    Subscribe
                  </Button>
                </Form>
                <p className="newsletter-note">
                  I respect your privacy. Unsubscribe anytime.
                </p>
              </div>
            </Col>
          </Row>
        </div>
      </Container>
    </div>
  );
};

export default Blog;
