import "./App.css";
import LuxuryLoader from "./Components/LuxuryLoader";
import LuxuryTerminal from "./Components/LuxuryTerminal";
import GlobalParticleSystem from "./Components/GlobalParticleSystem";
import { initGA, trackPageView } from './utils/analytics';
import React, { useState, useEffect } from 'react';

import "bootstrap/dist/css/bootstrap.min.css";
import {
  BrowserRouter as Router,
  useLocation,
  useHistory,
} from "react-router-dom";

// Layout Components
import Navbar from "./Components/Layout/Navbar";
import Footer from "./Components/Layout/Footer";

// Pages
import Home from "./pages/Home/Home";
import Projects from "./pages/Projects/Projects";
import ProjectDetails from "./pages/Projects/ProjectDetails";
import Experience from "./pages/Experience/Experience";
import Publications from "./pages/Publications/Publications";
// import SpaceCorner from "./pages/SpaceCorner/SpaceCorner";
import Blog from "./pages/Blog/Blog";
import BlogPost from "./pages/Blog/BlogPost";
import Contact from "./pages/Contact/Contact";
import Resume from './pages/resume';
import Play from './pages/Play/Play';
import NotFound from './pages/NotFound/NotFound';

const AppContent = ({ darkMode, setDarkMode }) => {
  const [showTerminal, setShowTerminal] = useState(false);
  const location = useLocation();
  const history = useHistory();

  // Parse query parameters
  const urlParams = new URLSearchParams(location.search);
  const currentPage = urlParams.get('page') || 'home';
  const projectId = urlParams.get('project');
  const blogId = urlParams.get('blog');

  // Check if current page should hide layout components
  const isPlayPage = currentPage === 'play';

  // Navigation helper function
  const navigateTo = (page, params = {}) => {
    const searchParams = new URLSearchParams({ page, ...params });
    history.push(`/?${searchParams.toString()}`);
  };

  useEffect(() => {
    // Track page views with query params
    trackPageView(`/?page=${currentPage}`);
  }, [currentPage]);

  // Render current page component based on query parameters
  const renderCurrentPage = () => {
    switch (currentPage) {
      case 'home':
        return <Home navigateTo={navigateTo} />;
      case 'projects':
        return projectId ?
          <ProjectDetails projectId={projectId} navigateTo={navigateTo} /> :
          <Projects navigateTo={navigateTo} />;
      case 'experience':
        return <Experience navigateTo={navigateTo} />;
      case 'publications':
        return <Publications navigateTo={navigateTo} />;
      case 'blog':
        return blogId ?
          <BlogPost blogId={blogId} navigateTo={navigateTo} /> :
          <Blog navigateTo={navigateTo} />;
      case 'contact':
        return <Contact navigateTo={navigateTo} />;
      case 'resume':
        return <Resume navigateTo={navigateTo} />;
      case 'play':
        return <Play navigateTo={navigateTo} />;
      default:
        return <NotFound navigateTo={navigateTo} />;
    }
  };

  useEffect(() => {

    // Terminal keyboard shortcut (Ctrl + `) - disabled on play page
    const handleKeyDown = (e) => {
      if (isPlayPage) return; // Disable terminal on play page
      
      if (e.ctrlKey && e.key === '`') {
        e.preventDefault();
        setShowTerminal(!showTerminal);
      }
      if (e.key === 'Escape' && showTerminal) {
        setShowTerminal(false);
      }
    };

    document.addEventListener('keydown', handleKeyDown);
    return () => document.removeEventListener('keydown', handleKeyDown);
  }, [showTerminal, isPlayPage]);


  return (
    <div className={`app-container ${darkMode ? "dark-mode" : "light-mode"}`}>
      {/* {!isPlayPage && <StarField starCount={150} speed={0.5} />} */}
      {!isPlayPage && <GlobalParticleSystem />}
      {!isPlayPage && (
        <Navbar
          darkMode={darkMode}
          toggleDarkMode={() => setDarkMode(!darkMode)}
          onTerminalToggle={() => setShowTerminal(!showTerminal)}
          navigateTo={navigateTo}
          currentPage={currentPage}
        />
      )}
      <div className={isPlayPage ? "" : "content-wrap"}>
        {renderCurrentPage()}
      </div>
      {!isPlayPage && <Footer navigateTo={navigateTo} />}
      
      {!isPlayPage && (
        <LuxuryTerminal
          isVisible={showTerminal}
          onClose={() => setShowTerminal(false)}
          navigateTo={navigateTo}
        />
      )}
    </div>
  );
};

const App = () => {
  const [appState, setAppState] = useState('loading'); // 'loading', 'transitioning', 'ready'
  const [darkMode, setDarkMode] = useState(true); // Global theme state

  useEffect(() => {
    // Initialize Google Analytics
    initGA();

    // Check if we should show loading screen (once per 5 minutes)
    const lastLoadTime = localStorage.getItem('lastLuxuryLoadTime');
    const currentTime = Date.now();
    const fiveMinutes = 24 * 60 * 60 * 1000; // 1 day
    
    if (lastLoadTime && (currentTime - parseInt(lastLoadTime)) < fiveMinutes) {
      // Skip loading screen if shown recently
      setAppState('ready');
    } else {
      // Show loading screen and update timestamp
      localStorage.setItem('lastLuxuryLoadTime', currentTime.toString());
      setAppState('loading');
    }

    // Check user's preferred color scheme, prefer dark if no preference
    const prefersDarkMode = window.matchMedia("(prefers-color-scheme: dark)").matches;
    const prefersLightMode = window.matchMedia("(prefers-color-scheme: light)").matches;
    
    if (prefersLightMode && !prefersDarkMode) {
      setDarkMode(false);
    } else {
      setDarkMode(true); // Default to dark or honor dark preference
    }
  }, []);

  const handleLoadingComplete = () => {
    setAppState('transitioning');
    setTimeout(() => {
      setAppState('ready');
    }, 1500);
  };

  return (
    <div className={`app-wrapper ${appState} ${darkMode ? "dark-mode" : "light-mode"}`}>

      {/* Loader */}
      {appState === 'loading' && (
        <div className="loader-layer">
          <LuxuryLoader onComplete={handleLoadingComplete} />
        </div>
      )}

      {/* Main Content */}
      {(appState === 'transitioning' || appState === 'ready') && (
        <Router>
          <div className="content-layer">
            <AppContent darkMode={darkMode} setDarkMode={setDarkMode} />
          </div>
        </Router>
      )}
    </div>
  );
};

export default App;
