// Replace with your Google Analytics Measurement ID
const GA_MEASUREMENT_ID = process.env.REACT_APP_GA_MEASUREMENT_ID;

// Initialize Google Analytics
export const initGA = () => {
  // GA is already initialized via the script tag in index.html
  console.log('Google Analytics initialized with ID:', GA_MEASUREMENT_ID);
};

// Track page views
export const trackPageView = (path) => {
  if (typeof window.gtag !== 'undefined' && GA_MEASUREMENT_ID) {
    window.gtag('config', GA_MEASUREMENT_ID, {
      page_path: path,
    });
  }
};

// Track custom events
export const trackEvent = (action, category, label, value) => {
  if (typeof window.gtag !== 'undefined') {
    window.gtag('event', action, {
      event_category: category,
      event_label: label,
      value: value,
    });
  }
};

// Track specific portfolio events
export const trackProjectView = (projectName) => {
  trackEvent('view_project', 'portfolio', projectName);
};

export const trackContactAttempt = (method) => {
  trackEvent('contact_attempt', 'engagement', method);
};

export const trackResumeDownload = () => {
  trackEvent('download', 'resume', 'pdf');
};

export const trackSocialClick = (platform) => {
  trackEvent('social_click', 'external_link', platform);
};

// Track Web Vitals performance metrics
export const trackWebVital = ({ name, value, id }) => {
  if (typeof window.gtag !== 'undefined') {
    window.gtag('event', name, {
      event_category: 'Web Vitals',
      event_label: id,
      value: Math.round(name === 'CLS' ? value * 1000 : value),
      non_interaction: true,
    });
  }
};