import React, { useEffect, useRef, useState, useCallback } from 'react';
import './Play.css';
import Particle from './components/Particle';

const Play = () => {
  const canvasRef = useRef(null);
  const [dimensions, setDimensions] = useState({ width: 0, height: 0 });
  const [, setParticles] = useState([]);
  const [isDragging, setIsDragging] = useState(false);
  const [dragStart, setDragStart] = useState(null);
  const [dragCurrent, setDragCurrent] = useState(null);
  const [selectedType, setSelectedType] = useState('planet');
  const [timeScale, setTimeScale] = useState(5);
  const [distanceScale, setDistanceScale] = useState(20);
  const [debugMode, setDebugMode] = useState(false);
  const [isPaused, setIsPaused] = useState(false);
  const [gravityEnabled, setGravityEnabled] = useState(true);
  const [showOrbitalPaths, setShowOrbitalPaths] = useState(false);
  const [hoveredParticle, setHoveredParticle] = useState(null);
  const [mousePos, setMousePos] = useState({ x: 0, y: 0 });
  const [currentMass, setCurrentMass] = useState(1.0);
  const [isHolding, setIsHolding] = useState(false);
  const [showInstructions, setShowInstructions] = useState(true);
  const animationFrameRef = useRef();
  const particlesRef = useRef([]);
  const holdStartTimeRef = useRef(null);

  useEffect(() => {
    const debugFlag = localStorage.getItem('orbital-debug-mode') === 'true';
    setDebugMode(debugFlag);
  }, []);

  const calculateMassFromHoldTime = useCallback((holdTime) => {
    const cycleTime = 12000;
    const timeInCycle = holdTime % cycleTime;
    
    let mass;
    if (timeInCycle <= 5000) {
      mass = 1 + (99 * timeInCycle / 5000);
    } else if (timeInCycle <= 6000) {
      mass = 100;
    } else if (timeInCycle <= 11000) {
      mass = 100 - (99 * (timeInCycle - 6000) / 5000);
    } else {
      mass = 1;
    }
    
    return Math.max(1, mass);
  }, []);

  const createThreeBodySystem = useCallback(() => {
    if (!canvasRef.current) return;
    
    const canvas = canvasRef.current;
    const centerX = canvas.width / 2;
    const centerY = canvas.height / 2;
    
    const particles = [
      new Particle({
        x: centerX,
        y: centerY - 100,
        mass: 10,
        type: 'star',
        canvas: canvas
      }),
      new Particle({
        x: centerX - 150,
        y: centerY + 50,
        mass: 8,
        type: 'planet',
        canvas: canvas
      }),
      new Particle({
        x: centerX + 150,
        y: centerY + 50,
        mass: 6,
        type: 'comet',
        canvas: canvas
      })
    ];
    
    particles[0].vx = 0;
    particles[0].vy = 0;
    particles[1].vx = 0;
    particles[1].vy = 0;
    particles[2].vx = 0;
    particles[2].vy = 0;
    
    particlesRef.current = particles;
    setParticles(particles);
  }, []);

  const createTwoBodySystem = useCallback(() => {
    if (!canvasRef.current) return;
    
    const canvas = canvasRef.current;
    const centerX = canvas.width / 2;
    const centerY = canvas.height / 2;
    
    const particles = [
      new Particle({
        x: centerX - 200,
        y: centerY,
        mass: 15,
        type: 'star',
        canvas: canvas
      }),
      new Particle({
        x: centerX + 200,
        y: centerY,
        mass: 5,
        type: 'planet',
        canvas: canvas
      })
    ];
    
    particles[0].vx = 0;
    particles[0].vy = 0;
    particles[1].vx = 0;
    particles[1].vy = 0;
    
    particlesRef.current = particles;
    setParticles(particles);
  }, []);

  const createSolarSystem = useCallback(() => {
    if (!canvasRef.current) return;
    
    const canvas = canvasRef.current;
    const centerX = canvas.width / 2;
    const centerY = canvas.height / 2;
    
    const auScale = 25;
    const G = 0.001;
    
    const sun = new Particle({
      x: centerX,
      y: centerY,
      mass: 1,
      type: 'sun',
      canvas: canvas,
      useRealisticMass: true
    });
    
    const planets = [
      { 
        type: 'mercury', 
        semiMajorAxis: 0.39 * auScale, 
        eccentricity: 0.206,
        orbitalPeriod: 0.24,
        startingAnomaly: 0
      },
      { 
        type: 'venus', 
        semiMajorAxis: 0.72 * auScale, 
        eccentricity: 0.007,
        orbitalPeriod: 0.62,
        startingAnomaly: Math.PI / 4
      },
      { 
        type: 'earth', 
        semiMajorAxis: 1.0 * auScale, 
        eccentricity: 0.017,
        orbitalPeriod: 1.0,
        startingAnomaly: Math.PI / 2
      },
      { 
        type: 'mars', 
        semiMajorAxis: 1.52 * auScale, 
        eccentricity: 0.093,
        orbitalPeriod: 1.88,
        startingAnomaly: Math.PI
      },
      { 
        type: 'jupiter', 
        semiMajorAxis: 5.2 * auScale, 
        eccentricity: 0.049,
        orbitalPeriod: 11.86,
        startingAnomaly: 3 * Math.PI / 2
      },
      { 
        type: 'saturn', 
        semiMajorAxis: 9.5 * auScale, 
        eccentricity: 0.057,
        orbitalPeriod: 29.46,
        startingAnomaly: Math.PI / 6
      },
      { 
        type: 'uranus', 
        semiMajorAxis: 19.2 * auScale, 
        eccentricity: 0.046,
        orbitalPeriod: 84.01,
        startingAnomaly: Math.PI / 3
      },
      { 
        type: 'neptune', 
        semiMajorAxis: 30.1 * auScale, 
        eccentricity: 0.010,
        orbitalPeriod: 164.8,
        startingAnomaly: 2 * Math.PI / 3
      },
      { 
        type: 'pluto', 
        semiMajorAxis: 39.5 * auScale, 
        eccentricity: 0.244,
        orbitalPeriod: 248.1,
        startingAnomaly: 5 * Math.PI / 6
      }
    ];
    
    const particles = [sun];
    
    planets.forEach((planetData) => {
      const trueAnomaly = planetData.startingAnomaly;
      
      const r = planetData.semiMajorAxis * (1 - planetData.eccentricity * planetData.eccentricity) / 
                (1 + planetData.eccentricity * Math.cos(trueAnomaly));
      
      const x = centerX + r * Math.cos(trueAnomaly);
      const y = centerY + r * Math.sin(trueAnomaly);
      
      const planet = new Particle({
        x: x,
        y: y,
        mass: 1,
        type: planetData.type,
        canvas: canvas,
        useRealisticMass: true
      });
      
      const specificAngularMomentum = Math.sqrt(G * sun.mass * planetData.semiMajorAxis * (1 - planetData.eccentricity * planetData.eccentricity));
      const orbitalVelocity = specificAngularMomentum / r;
      
      const velocityAngle = trueAnomaly + Math.PI / 2 + Math.atan(planetData.eccentricity * Math.sin(trueAnomaly) / (1 + planetData.eccentricity * Math.cos(trueAnomaly)));
      
      planet.vx = orbitalVelocity * Math.cos(velocityAngle) * 0.7;
      planet.vy = orbitalVelocity * Math.sin(velocityAngle) * 0.7;
      
      particles.push(planet);
    });
    
    particlesRef.current = particles;
    setParticles(particles);
  }, []);

  const findHoveredParticle = useCallback((x, y) => {
    if (!isPaused) return null;
    
    for (let particle of particlesRef.current) {
      const dx = x - particle.x;
      const dy = y - particle.y;
      const distance = Math.sqrt(dx * dx + dy * dy);
      
      if (distance <= particle.getPhysicalRadius() + 10) {
        return particle;
      }
    }
    return null;
  }, [isPaused]);

  useEffect(() => {
    const updateDimensions = () => {
      setDimensions({
        width: window.innerWidth,
        height: window.innerHeight
      });
    };

    updateDimensions();
    window.addEventListener('resize', updateDimensions);
    return () => window.removeEventListener('resize', updateDimensions);
  }, []);

  useEffect(() => {
    if (!isPaused) {
      setHoveredParticle(null);
    }
  }, [isPaused]);

  useEffect(() => {
    let intervalId;
    
    if (isHolding && holdStartTimeRef.current) {
      console.log('Starting mass update loop');
      intervalId = setInterval(() => {
        const elapsed = Date.now() - holdStartTimeRef.current;
        const holdTimeSeconds = elapsed / 1000;
        const newMass = calculateMassFromHoldTime(elapsed);
        console.log('Hold time:', holdTimeSeconds.toFixed(2), 's | Mass:', newMass.toFixed(1));
        setCurrentMass(newMass);
      }, 50);
    }
    
    return () => {
      if (intervalId) {
        console.log('Clearing mass update interval');
        clearInterval(intervalId);
      }
    };
  }, [isHolding, calculateMassFromHoldTime]);

  const handleMouseDown = useCallback((e) => {
    console.log('Mouse down detected');
    const rect = canvasRef.current.getBoundingClientRect();
    const x = e.clientX - rect.left;
    const y = e.clientY - rect.top;
    
    setIsDragging(true);
    setDragStart({ x, y });
    setDragCurrent({ x, y });
    
    const startTime = Date.now();
    holdStartTimeRef.current = startTime;
    setCurrentMass(1.0);
    setIsHolding(true);
    
    console.log('Starting hold at:', startTime);
  }, []);

  const handleMouseMove = useCallback((e) => {
    const rect = canvasRef.current.getBoundingClientRect();
    const x = e.clientX - rect.left;
    const y = e.clientY - rect.top;
    
    setMousePos({ x: e.clientX, y: e.clientY });
    
    if (isDragging) {
      setDragCurrent({ x, y });
    } else if (isPaused) {
      const hovered = findHoveredParticle(x, y);
      setHoveredParticle(hovered);
    } else {
      setHoveredParticle(null);
    }
  }, [isDragging, isPaused, findHoveredParticle]);

  const handleMouseUp = useCallback(() => {
    console.log('Mouse up detected');
    setIsHolding(false);
    holdStartTimeRef.current = null;
    
    if (!isDragging || !dragStart) {
      setCurrentMass(1.0);
      setIsDragging(false);
      setDragStart(null);
      setDragCurrent(null);
      return;
    }
    
    console.log('Creating particle - Mass:', currentMass.toFixed(1));
    
    const dx = dragCurrent ? (dragCurrent.x - dragStart.x) * 0.01 : 0;
    const dy = dragCurrent ? (dragCurrent.y - dragStart.y) * 0.01 : 0;
    
    const newParticle = new Particle({
      x: dragStart.x,
      y: dragStart.y,
      mass: currentMass,
      type: selectedType,
      canvas: canvasRef.current
    });
    
    newParticle.vx = dx * timeScale;
    newParticle.vy = dy * timeScale;
    
    const newParticles = [...particlesRef.current, newParticle];
    particlesRef.current = newParticles;
    setParticles(newParticles);
    
    setIsDragging(false);
    setDragStart(null);
    setDragCurrent(null);
    setCurrentMass(1.0);
  }, [isDragging, dragStart, dragCurrent, selectedType, currentMass, timeScale]);

  useEffect(() => {
    if (!canvasRef.current) return;

    const canvas = canvasRef.current;
    canvas.addEventListener('mousedown', handleMouseDown);
    canvas.addEventListener('mousemove', handleMouseMove);
    canvas.addEventListener('mouseup', handleMouseUp);

    return () => {
      canvas.removeEventListener('mousedown', handleMouseDown);
      canvas.removeEventListener('mousemove', handleMouseMove);
      canvas.removeEventListener('mouseup', handleMouseUp);
    };
  }, [handleMouseDown, handleMouseMove, handleMouseUp]);

  useEffect(() => {
    if (!canvasRef.current || dimensions.width === 0) return;

    const canvas = canvasRef.current;
    const ctx = canvas.getContext('2d');
    
    canvas.width = dimensions.width;
    canvas.height = dimensions.height;

    const animate = () => {
      ctx.fillStyle = '#0a0a0a';
      ctx.fillRect(0, 0, canvas.width, canvas.height);

      if (!isPaused) {
        if (gravityEnabled && particlesRef.current.length > 1) {
          const particlesToRemove = [];
          
          for (let i = 0; i < particlesRef.current.length; i++) {
            for (let j = i + 1; j < particlesRef.current.length; j++) {
              const particle1 = particlesRef.current[i];
              const particle2 = particlesRef.current[j];
              
              if (particle1.shouldBeDestroyed(particle2)) {
                if (!particlesToRemove.includes(i)) particlesToRemove.push(i);
              } else if (particle2.shouldBeDestroyed(particle1)) {
                if (!particlesToRemove.includes(j)) particlesToRemove.push(j);
              } else {
                const force = particle1.calculateGravitationalForce(particle2);
                
                if (!force.collision) {
                  particle1.applyForce(force.fx, force.fy);
                  particle2.applyForce(-force.fx, -force.fy);
                }
              }
            }
          }
          
          particlesToRemove.sort((a, b) => b - a);
          particlesToRemove.forEach(index => {
            particlesRef.current.splice(index, 1);
          });
          
          if (particlesToRemove.length > 0) {
            setParticles([...particlesRef.current]);
          }
        }
        
        particlesRef.current.forEach(particle => {
          particle.update(timeScale, 0.016, showOrbitalPaths);
        });
      }

      particlesRef.current.forEach(particle => {
        particle.render(ctx, distanceScale, showOrbitalPaths);
      });

      if (isDragging && dragStart) {
        ctx.save();
        
        // Draw velocity line
        if (dragCurrent) {
          ctx.strokeStyle = '#fff';
          ctx.lineWidth = 2;
          ctx.setLineDash([5, 5]);
          
          const gradient = ctx.createLinearGradient(dragStart.x, dragStart.y, dragCurrent.x, dragCurrent.y);
          gradient.addColorStop(0, '#ffffff');
          gradient.addColorStop(1, 'transparent');
          ctx.strokeStyle = gradient;
          
          ctx.beginPath();
          ctx.moveTo(dragStart.x, dragStart.y);
          ctx.lineTo(dragCurrent.x, dragCurrent.y);
          ctx.stroke();
        }
        
        // Draw mass preview circle
        ctx.setLineDash([]);
        const previewRadius = Math.max(3, Math.min(20, Math.sqrt(currentMass) * 2));
        const alpha = 0.6;
        
        // Get color based on mass
        const getMassColor = (mass) => {
          if (mass <= 5) return '#8B4513';      // Brown dwarf
          else if (mass <= 15) return '#CD853F'; // Small star
          else if (mass <= 35) return '#FFD700'; // Yellow star
          else if (mass <= 60) return '#FFA500'; // Orange star
          else if (mass <= 85) return '#FF6347'; // Red giant
          else return '#DC143C';                 // Red supergiant
        };
        
        const previewColor = getMassColor(currentMass);
        
        // Glow effect
        ctx.shadowBlur = 15;
        ctx.shadowColor = previewColor;
        
        // Circle fill
        ctx.globalAlpha = alpha;
        ctx.fillStyle = previewColor;
        ctx.beginPath();
        ctx.arc(dragStart.x, dragStart.y, previewRadius, 0, Math.PI * 2);
        ctx.fill();
        
        // Circle border
        ctx.globalAlpha = 1;
        ctx.strokeStyle = previewColor;
        ctx.lineWidth = 2;
        ctx.beginPath();
        ctx.arc(dragStart.x, dragStart.y, previewRadius, 0, Math.PI * 2);
        ctx.stroke();
        
        ctx.restore();
      }

      animationFrameRef.current = requestAnimationFrame(animate);
    };
    animate();

    return () => {
      if (animationFrameRef.current) {
        cancelAnimationFrame(animationFrameRef.current);
      }
    };
  }, [dimensions, isDragging, dragStart, dragCurrent, isPaused, timeScale, distanceScale, gravityEnabled, showOrbitalPaths, currentMass]);

  return (
    <div className="play-page">
      <canvas
        ref={canvasRef}
        className="play-canvas"
        style={{ 
          width: dimensions.width, 
          height: dimensions.height 
        }}
      />
      
      <div className="play-ui">
        <div className="mass-indicator">
          {isDragging && (
            <div className="mass-display">
              Mass: {currentMass.toFixed(1)}
            </div>
          )}
        </div>
        
        {debugMode && (
          <div className="debug-controls">
          <div className="control-group">
            <label>Type:</label>
            <select value={selectedType} onChange={(e) => setSelectedType(e.target.value)}>
              <option value="planet">Planet</option>
              <option value="star">Star</option>
              <option value="comet">Comet</option>
              <option value="object">Object</option>
              <option value="sun">Sun</option>
              <option value="mercury">Mercury</option>
              <option value="venus">Venus</option>
              <option value="earth">Earth</option>
              <option value="mars">Mars</option>
              <option value="jupiter">Jupiter</option>
              <option value="saturn">Saturn</option>
              <option value="uranus">Uranus</option>
              <option value="neptune">Neptune</option>
              <option value="pluto">Pluto</option>
            </select>
          </div>
          
          <div className="control-group">
            <label>Time Scale: {timeScale.toFixed(1)}</label>
            <input 
              type="range" 
              min="0.1" 
              max="1000" 
              step="0.1"
              value={timeScale} 
              onChange={(e) => setTimeScale(Number(e.target.value))}
            />
          </div>
          
          <div className="control-group">
            <label>Distance Scale: {distanceScale}px/AU</label>
            <input 
              type="range" 
              min="5" 
              max="50" 
              value={distanceScale} 
              onChange={(e) => setDistanceScale(Number(e.target.value))}
            />
          </div>
          
          <div className="control-group">
            <button onClick={() => setIsPaused(!isPaused)}>
              {isPaused ? 'Resume' : 'Pause'}
            </button>
            <button onClick={() => { particlesRef.current = []; setParticles([]); }}>
              Clear
            </button>
            <button onClick={createTwoBodySystem}>
              2-Body Test
            </button>
            <button onClick={createThreeBodySystem}>
              3-Body System
            </button>
            <button onClick={createSolarSystem}>
              Solar System
            </button>
          </div>
          
          <div className="control-group">
            <label>
              <input 
                type="checkbox" 
                checked={gravityEnabled} 
                onChange={(e) => setGravityEnabled(e.target.checked)}
              />
              Enable Gravity
            </label>
          </div>
          
          <div className="control-group">
            <label>
              <input 
                type="checkbox" 
                checked={showOrbitalPaths} 
                onChange={(e) => setShowOrbitalPaths(e.target.checked)}
              />
              Show Orbital Paths
            </label>
          </div>
          
          <div className="instruction-text">
            Drag to create particles with initial velocity. Hold to increase mass (1-100).
            {isPaused && <><br/>Hover over particles for details</>}
          </div>
          </div>
        )}
        
        {hoveredParticle && isPaused && (
          <div 
            className="particle-tooltip"
            style={{
              left: mousePos.x + 15,
              top: mousePos.y - 10
            }}
          >
            {(() => {
              const sunParticle = particlesRef.current.find(p => p.type === 'sun');
              const info = hoveredParticle.getDetailedInfo(sunParticle);
              
              return (
                <div>
                  <div className="tooltip-title">{info.name}</div>
                  <div className="tooltip-item">Mass: {info.mass}</div>
                  <div className="tooltip-item">Velocity: {info.velocity}</div>
                  <div className="tooltip-item">Acceleration: {info.acceleration}</div>
                  <div className="tooltip-item">Position: {info.position}</div>
                  <div className="tooltip-item">Radius: {info.physicalRadius}</div>
                  {info.distanceFromSun && (
                    <>
                      <div className="tooltip-item">Distance from Sun: {info.distanceFromSun}</div>
                      <div className="tooltip-item">Orbital Velocity: {info.orbitalVelocity}</div>
                    </>
                  )}
                </div>
              );
            })()}
          </div>
        )}
      </div>

      {showInstructions && (
        <div className="instructions-overlay">
          <div className="instructions-modal">
            <h2>Welcome to the Planetary Playground!</h2>
            <div className="instructions-content">
              <p>Create your own solar system by adding celestial bodies:</p>
              <ul>
                <li><strong>Click and drag</strong> on the canvas to create a new planet</li>
                <li><strong>Hold longer</strong> to increase the mass (1-100)</li>
                <li><strong>Drag direction</strong> determines the initial velocity</li>
                <li>Watch as gravity affects the motion of your planets!</li>
              </ul>
              <p className="instructions-tip">Tip: Try creating a large star in the center and smaller planets around it!</p>
            </div>
            <button
              className="instructions-close"
              onClick={() => setShowInstructions(false)}
            >
              Got it!
            </button>
          </div>
        </div>
      )}
    </div>
  );
};

export default Play;