import React from 'react';
import { FaMoon, FaSun } from 'react-icons/fa';
import './SpaceThemeToggle.css';

const SpaceThemeToggle = ({ darkMode, toggleDarkMode }) => {
  return (
    <div className="space-theme-toggle" onClick={toggleDarkMode}>
      <div className={`toggle-track ${darkMode ? 'dark' : 'light'}`}>
        <div className="toggle-indicator">
          {darkMode ? (
            <FaMoon className="toggle-icon moon" />
          ) : (
            <FaSun className="toggle-icon sun" />
          )}
        </div>
      </div>
      <span className="toggle-label">
        {darkMode ? 'Dark Space' : 'Light Space'}
      </span>
    </div>
  );
};

export default SpaceThemeToggle;
