import { useEffect } from 'react';
import './GlobalParticleSystem.css';

let particleSystemCreated = false;

const GlobalParticleSystem = () => {
  useEffect(() => {
    // Only create the particle system once globally
    if (!particleSystemCreated && !document.getElementById('global-particles')) {
      const particleContainer = document.createElement('div');
      particleContainer.id = 'global-particles';
      particleContainer.className = 'global-particle-system';
      
      // Create particles
      for (let i = 0; i < 20; i++) {
        const particle = document.createElement('div');
        particle.className = `particle particle-${i}`;
        particleContainer.appendChild(particle);
      }
      
      // Append to body so it persists across all component changes
      document.body.appendChild(particleContainer);
      particleSystemCreated = true;
    }

    return () => {
      // Don't cleanup - let particles persist
    };
  }, []);

  return null; // We don't render anything in React, particles are in DOM directly
};

export default GlobalParticleSystem;