import React, { useState, useEffect } from "react";
import { Container } from "react-bootstrap";
import { FaGithub, FaLinkedin, FaFilePdf } from "react-icons/fa";
import SpaceThemeToggle from "../SpaceThemeToggle";
import * as Constants from "../../Constants/Constants";
import "./Navbar.css";

const Navbar = ({ darkMode, toggleDarkMode, onTerminalToggle, navigateTo, currentPage }) => {
  const [expanded, setExpanded] = useState(false);
  const [scrolled, setScrolled] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      if (window.scrollY > 50) {
        setScrolled(true);
      } else {
        setScrolled(false);
      }
    };

    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  const closeNavbar = () => {
    setExpanded(false);
  };

  return (
    <nav className={`luxury-navbar ${scrolled ? "scrolled" : ""}`}>
      <div className="navbar-backdrop"></div>
      
      <Container>
        <div className="navbar-content">
          {/* Luxury Brand Section */}
          <button
            className="luxury-brand"
            onClick={() => {
              navigateTo('home');
              closeNavbar();
            }}
          >
            <div className="brand-logo">
              <div className="logo-ring"></div>
              <img
                src={`${window.location.origin}/logo192.png`}
                alt="SS Logo"
                className="logo-rocket"
                onError={(e) => {
                  e.target.style.display = 'none';
                  e.target.nextSibling.style.display = 'inline';
                }}
              />
              <span className="logo-initials" style={{display: 'none'}}>SS</span>
            </div>
            <div className="brand-info">
              <div className="brand-name">Surdi</div>
            </div>
          </button>

          {/* Central Navigation */}
          <div className="nav-center">
            <div className="nav-items">
              <button
                className={`nav-item ${currentPage === 'home' ? 'active' : ''}`}
                onClick={() => {
                  navigateTo('home');
                  closeNavbar();
                }}
              >
                <span className="nav-text">Portfolio</span>
                <div className="nav-indicator"></div>
              </button>
              
              <button
                className={`nav-item ${currentPage === 'projects' ? 'active' : ''}`}
                onClick={() => {
                  navigateTo('projects');
                  closeNavbar();
                }}
              >
                <span className="nav-text">Projects</span>
                <div className="nav-indicator"></div>
              </button>
              
              <button
                className={`nav-item ${currentPage === 'experience' ? 'active' : ''}`}
                onClick={() => {
                  navigateTo('experience');
                  closeNavbar();
                }}
              >
                <span className="nav-text">Experience</span>
                <div className="nav-indicator"></div>
              </button>
              
              <button
                className={`nav-item ${currentPage === 'publications' ? 'active' : ''}`}
                onClick={() => {
                  navigateTo('publications');
                  closeNavbar();
                }}
              >
                <span className="nav-text">Research</span>
                <div className="nav-indicator"></div>
              </button>
              
              <button
                className={`nav-item ${currentPage === 'contact' ? 'active' : ''}`}
                onClick={() => {
                  navigateTo('contact');
                  closeNavbar();
                }}
              >
                <span className="nav-text">Connect</span>
                <div className="nav-indicator"></div>
              </button>
            </div>
          </div>

          {/* Luxury Actions */}
          <div className="navbar-luxury-actions">
            <div className="action-group">
              <a 
                href={Constants.GITHUB_PROFILE} 
                target="_blank" 
                rel="noopener noreferrer" 
                className="luxury-icon"
                title="GitHub"
              >
                <FaGithub />
              </a>
              
              <a 
                href={Constants.LINKED_IN_PROFILE} 
                target="_blank" 
                rel="noopener noreferrer" 
                className="luxury-icon"
                title="LinkedIn"
              >
                <FaLinkedin />
              </a>
              
              <a 
                href={Constants.LINK_TO_RESUME} 
                target="_blank" 
                rel="noopener noreferrer" 
                className="luxury-icon resume-icon"
                title="Resume (PDF)"
              >
                <FaFilePdf />
              </a>
            </div>

            <div className="action-separator"></div>

            <div 
              className="terminal-access" 
              title="Access Terminal Interface"
              onClick={onTerminalToggle}
            >
              <div className="terminal-visual">
                <div className="terminal-lines">
                  <div className="terminal-line"></div>
                  <div className="terminal-line"></div>
                  <div className="terminal-line"></div>
                </div>
              </div>
              <span className="terminal-label">Terminal</span>
            </div>

            <SpaceThemeToggle darkMode={darkMode} toggleDarkMode={toggleDarkMode} />
          </div>

          {/* Mobile Menu Toggle */}
          <button 
            className={`mobile-toggle ${expanded ? "active" : ""}`}
            onClick={() => setExpanded(!expanded)}
          >
            <span></span>
            <span></span>
            <span></span>
          </button>
        </div>

        {/* Mobile Navigation */}
        <div className={`mobile-nav ${expanded ? "active" : ""}`}>
          <div className="mobile-nav-content">
            <button className="mobile-nav-item" onClick={() => { navigateTo('home'); closeNavbar(); }}>
              Portfolio
            </button>
            <button className="mobile-nav-item" onClick={() => { navigateTo('projects'); closeNavbar(); }}>
              Projects
            </button>
            <button className="mobile-nav-item" onClick={() => { navigateTo('experience'); closeNavbar(); }}>
              Experience
            </button>
            <button className="mobile-nav-item" onClick={() => { navigateTo('publications'); closeNavbar(); }}>
              Research
            </button>
            <button className="mobile-nav-item" onClick={() => { navigateTo('contact'); closeNavbar(); }}>
              Connect
            </button>
          </div>
        </div>
      </Container>
    </nav>
  );
};

export default Navbar;
