import React, { useEffect, useState } from "react";
import { Container, Row, Col, Button, Badge } from "react-bootstrap";
import { FaCalendarAlt, FaClock, FaTags, FaArrowLeft, FaLinkedin, FaTwitter, FaLink } from "react-icons/fa";
import * as Constants from "../../Constants/Constants";
import "./BlogPost.css";

const BlogPost = ({ blogId, navigateTo }) => {
  const [post, setPost] = useState(null);
  const [loading, setLoading] = useState(true);
  const [copied, setCopied] = useState(false);

  // Scroll to top on load
  useEffect(() => {
    window.scrollTo(0, 0);
  }, [blogId]);

  // Fetch blog post data
  useEffect(() => {
    setLoading(true);
    // In a real app, you would fetch data from an API
    // For now, we'll use our Constants data
    const foundPost = Constants.BLOG_POSTS.find((post) => post.id === blogId);

    if (foundPost) {
      setPost(foundPost);
      setLoading(false);
    } else {
      // Handle 404 - post not found
      setLoading(false);
    }
  }, [blogId]);

  // Handle copy link to clipboard
  const copyLink = () => {
    const url = window.location.href;
    navigator.clipboard.writeText(url).then(() => {
      setCopied(true);
      setTimeout(() => setCopied(false), 2000);
    });
  };

  // Share on social media
  const shareOn = (platform) => {
    let url = "";
    const pageUrl = encodeURIComponent(window.location.href);
    const title = encodeURIComponent(post ? post.title : "Blog Post");

    if (platform === "linkedin") {
      url = `https://www.linkedin.com/sharing/share-offsite/?url=${pageUrl}`;
    } else if (platform === "twitter") {
      url = `https://twitter.com/intent/tweet?url=${pageUrl}&text=${title}`;
    }

    if (url) {
      window.open(url, "_blank");
    }
  };

  if (loading) {
    return (
      <Container className="blog-post-loading">
        <div className="spinner"></div>
        <p>Loading article...</p>
      </Container>
    );
  }

  if (!post) {
    return (
      <Container className="blog-post-not-found">
        <h2>Article Not Found</h2>
        <p>The blog post you're looking for doesn't exist or has been removed.</p>
        <button onClick={() => navigateTo('blog')} className="btn btn-primary">
          <FaArrowLeft /> Back to Blog
        </button>
      </Container>
    );
  }

  return (
    <div className="blog-post-page">
      {/* Hero Header */}
      <div className="blog-post-header" style={{ backgroundImage: `url(${post.heroImage || post.image})` }}>
        <div className="blog-post-header-overlay">
          <Container>
            <div className="blog-post-header-content">
              <div className="blog-post-category-badge">
                <Badge bg="primary">{post.category}</Badge>
              </div>
              <h1>{post.title}</h1>
              <div className="blog-post-meta">
                <span className="blog-post-date">
                  <FaCalendarAlt /> {post.date}
                </span>
                <span className="blog-post-read-time">
                  <FaClock /> {post.readTime}
                </span>
              </div>
            </div>
          </Container>
        </div>
      </div>

      <Container className="blog-post-container">
        <Row>
          <Col lg={8} className="blog-post-content">
            <div className="blog-post-body">
              {/* For a real application, this content would be from a CMS */}
              {post.content ? (
                <div dangerouslySetInnerHTML={{ __html: post.content }} />
              ) : (
                <div className="placeholder-content">
                  <p>
                    This is placeholder content for the blog post. In a real application,
                    this content would be loaded from a CMS or database. The article would
                    discuss {post.title.toLowerCase()} in detail, providing valuable insights
                    and practical advice.
                  </p>
                  <h2>Key Points</h2>
                  <p>
                    The article would cover multiple key points related to {post.tags.join(", ")},
                    with detailed explanations, examples, and best practices.
                  </p>
                  <h2>Real-World Applications</h2>
                  <p>
                    There would be discussions about how these concepts apply in real-world
                    scenarios, with case studies and practical implementation strategies.
                  </p>
                  <h2>Conclusion</h2>
                  <p>
                    The article would conclude with a summary of the main points and
                    actionable takeaways for the reader.
                  </p>
                </div>
              )}
            </div>

            {/* Tags */}
            <div className="blog-post-tags-section">
              <FaTags className="icon" />
              {post.tags.map((tag, index) => (
                <Badge key={index} bg="secondary" className="blog-post-tag">
                  {tag}
                </Badge>
              ))}
            </div>

            {/* Share Section */}
            <div className="blog-post-share-section">
              <h3>Share this article</h3>
              <div className="blog-post-share-buttons">
                <Button
                  variant="outline-primary"
                  className="share-button linkedin"
                  onClick={() => shareOn("linkedin")}
                >
                  <FaLinkedin /> LinkedIn
                </Button>
                <Button
                  variant="outline-primary"
                  className="share-button twitter"
                  onClick={() => shareOn("twitter")}
                >
                  <FaTwitter /> Twitter
                </Button>
                <Button
                  variant="outline-primary"
                  className="share-button copy-link"
                  onClick={copyLink}
                >
                  <FaLink /> {copied ? "Copied!" : "Copy Link"}
                </Button>
              </div>
            </div>

            {/* Author Section */}
            <div className="blog-post-author-section">
              <div className="blog-post-author-avatar">
                <img
                  src={"https://via.placeholder.com/100x100?text=SS"}
                  alt="Swapnil Surdi"
                />
              </div>
              <div className="blog-post-author-info">
                <h3>Swapnil Surdi</h3>
                <p className="blog-post-author-bio">
                  Senior Software Engineer with 7+ years of experience in regulated healthcare
                  environments, full-stack development, and distributed systems. Expert in
                  quality systems and engineering leadership.
                </p>
                <div className="blog-post-author-links">
                  <a
                    href={Constants.LINKED_IN_PROFILE}
                    target="_blank"
                    rel="noopener noreferrer"
                    className="author-social linkedin"
                  >
                    <FaLinkedin />
                  </a>
                  <a
                    href={Constants.GITHUB_PROFILE}
                    target="_blank"
                    rel="noopener noreferrer"
                    className="author-social github"
                  >
                    <FaTwitter />
                  </a>
                </div>
              </div>
            </div>
          </Col>

          {/* Sidebar */}
          <Col lg={4} className="blog-post-sidebar">
            {/* Related Articles */}
            <div className="sidebar-section related-posts">
              <h3>Related Articles</h3>
              <div className="related-posts-list">
                {Constants.BLOG_POSTS
                  .filter(
                    (relatedPost) =>
                      relatedPost.id !== blogId &&
                      (relatedPost.category === post.category ||
                        relatedPost.tags.some((tag) => post.tags.includes(tag)))
                  )
                  .slice(0, 3)
                  .map((relatedPost, index) => (
                    <div key={index} className="related-post-item">
                      <button
                        onClick={() => navigateTo('blog', { blog: relatedPost.id })}
                        className="related-post-link"
                      >
                        <div className="related-post-image">
                          <img src={relatedPost.image} alt={relatedPost.title} />
                        </div>
                        <div className="related-post-info">
                          <h4>{relatedPost.title}</h4>
                          <span className="related-post-date">{relatedPost.date}</span>
                        </div>
                      </button>
                    </div>
                  ))}
              </div>
            </div>

            {/* Categories Section */}
            <div className="sidebar-section categories">
              <h3>Categories</h3>
              <div className="categories-list">
                {Array.from(
                  new Set(Constants.BLOG_POSTS.map((p) => p.category))
                ).map((category, index) => (
                  <button
                    key={index}
                    onClick={() => navigateTo('blog', { category })}
                    className="category-link"
                  >
                    {category}
                  </button>
                ))}
              </div>
            </div>

            {/* Newsletter Signup */}
            <div className="sidebar-section newsletter">
              <h3>Stay Updated</h3>
              <p>
                Subscribe to my newsletter for the latest articles on engineering
                leadership, quality systems, and technology.
              </p>
              <div className="newsletter-form">
                <input
                  type="email"
                  placeholder="Your email address"
                  className="newsletter-input"
                />
                <Button variant="primary" className="newsletter-button">
                  Subscribe
                </Button>
              </div>
              <p className="newsletter-note">
                No spam, unsubscribe anytime.
              </p>
            </div>
          </Col>
        </Row>

        {/* Navigation between posts */}
        <div className="blog-post-navigation">
          <Row>
            <Col xs={6}>
              {getPreviousPost() && (
                <button
                  onClick={() => navigateTo('blog', { blog: getPreviousPost().id })}
                  className="post-nav prev-post"
                >
                  <span className="post-nav-label">
                    <FaArrowLeft /> Previous
                  </span>
                  <span className="post-nav-title">{getPreviousPost().title}</span>
                </button>
              )}
            </Col>
            <Col xs={6} className="text-right">
              {getNextPost() && (
                <button
                  onClick={() => navigateTo('blog', { blog: getNextPost().id })}
                  className="post-nav next-post"
                >
                  <span className="post-nav-label">
                    Next <FaArrowLeft className="fa-flip-horizontal" />
                  </span>
                  <span className="post-nav-title">{getNextPost().title}</span>
                </button>
              )}
            </Col>
          </Row>
        </div>
      </Container>
    </div>
  );

  // Helper functions to get previous and next posts
  function getPreviousPost() {
    if (!post) return null;
    const currentIndex = Constants.BLOG_POSTS.findIndex((p) => p.id === blogId);
    return currentIndex > 0 ? Constants.BLOG_POSTS[currentIndex - 1] : null;
  }

  function getNextPost() {
    if (!post) return null;
    const currentIndex = Constants.BLOG_POSTS.findIndex((p) => p.id === blogId);
    return currentIndex < Constants.BLOG_POSTS.length - 1
      ? Constants.BLOG_POSTS[currentIndex + 1]
      : null;
  }
};

export default BlogPost;
