import React, { useState, useEffect } from 'react';
import './LuxuryLoader.css';

const LuxuryLoader = ({ onComplete }) => {
  const [greeting, setGreeting] = useState('Welcome');
  const [isExiting, setIsExiting] = useState(false);

  useEffect(() => {
    const greetings = [
      'Welcome',
      'Hello There',
      'Welcome to my world'
    ];

    let greetingIndex = 0;
    
    const greetingTimer = setInterval(() => {
      greetingIndex = (greetingIndex + 1) % greetings.length;
      setGreeting(greetings[greetingIndex]);
    }, 1500);

    // Start fade out after 3 seconds
    const fadeTimer = setTimeout(() => {
      clearInterval(greetingTimer);
      setIsExiting(true);
    }, 3000);

    // Complete after fade animation (additional 1 second)
    const completeTimer = setTimeout(() => {
      onComplete();
    }, 4000);

    return () => {
      clearInterval(greetingTimer);
      clearTimeout(fadeTimer);
      clearTimeout(completeTimer);
    };
  }, [onComplete]);


  return (
    <div className={`luxury-loader ${isExiting ? 'fade-out' : ''}`}>
      <div className="loader-background"></div>
      
      <div className="loader-content">
        <div className="logo-container">
          <div className="luxury-logo">
            <img src={`${window.location.origin}/logo192.png`} alt="Rocket Logo" className="logo-image" />
            <div className="logo-subtitle">ENGINEERING</div>
          </div>
        </div>

        <div className="greeting-section">
          <div className="greeting-text">
            {greeting}
          </div>
        </div>


        <div className="loader-footer">
          <div className="crafted-by">
            Handcrafted Digital Experience
          </div>
        </div>
      </div>

    </div>
  );
};

export default LuxuryLoader;