import React, { useState, useEffect } from 'react';
// No longer need useHistory - will use navigateTo prop
import './NotFound.css';

const NotFound = ({ navigateTo }) => {
  const [currentPhrase, setCurrentPhrase] = useState(0);
  const [, setShowTerminal] = useState(false);
  const [glitchActive, setGlitchActive] = useState(true);

  const errorPhrases = [
    "NAVIGATION SYSTEM OFFLINE",
    "COORDINATES NOT FOUND",
    "LOST IN DIGITAL SPACE",
    "TRAJECTORY MISCALCULATED",
    "DESTINATION UNREACHABLE"
  ];

  const suggestions = [
    { command: "nav home", description: "Return to mission control", action: () => navigateTo('home') },
    { command: "nav projects", description: "View project portfolio", action: () => navigateTo('projects') },
    { command: "nav experience", description: "Check career timeline", action: () => navigateTo('experience') },
    { command: "nav contact", description: "Establish communication", action: () => navigateTo('contact') },
    { command: "terminal", description: "Open command interface", action: () => setShowTerminal(true) }
  ];

  useEffect(() => {
    const phraseInterval = setInterval(() => {
      setCurrentPhrase(prev => (prev + 1) % errorPhrases.length);
    }, 2000);

    const glitchInterval = setInterval(() => {
      setGlitchActive(prev => !prev);
    }, 3000);

    // Auto-disable glitch after 10 seconds
    const glitchTimeout = setTimeout(() => {
      setGlitchActive(false);
    }, 10000);

    return () => {
      clearInterval(phraseInterval);
      clearInterval(glitchInterval);
      clearTimeout(glitchTimeout);
    };
  }, [errorPhrases.length]);

  const handleKeyPress = (e) => {
    if (e.ctrlKey && e.key === '`') {
      e.preventDefault();
      setShowTerminal(true);
    }
  };

  useEffect(() => {
    document.addEventListener('keydown', handleKeyPress);
    return () => document.removeEventListener('keydown', handleKeyPress);
  }, []);

  return (
    <div className="not-found-container">
      {/* Animated Background Particles */}
      <div className="error-particles">
        {[...Array(50)].map((_, i) => (
          <div
            key={i}
            className={`error-particle particle-${i % 5}`}
            style={{
              '--delay': `${Math.random() * 5}s`,
              '--duration': `${3 + Math.random() * 4}s`,
              '--x': `${Math.random() * 100}%`,
              '--y': `${Math.random() * 100}%`
            }}
          />
        ))}
      </div>

      {/* Main Error Display */}
      <div className="error-display">
        <div className="error-header">
          <div className={`error-code ${glitchActive ? 'glitch' : ''}`} data-text="404">
            404
          </div>
          <div className="error-phrase">
            {errorPhrases[currentPhrase]}
          </div>
        </div>

        <div className="system-status">
          <div className="status-line">
            <span className="status-label">STATUS:</span>
            <span className="status-value error">SYSTEM ERROR</span>
          </div>
          <div className="status-line">
            <span className="status-label">LOCATION:</span>
            <span className="status-value">{window.location.pathname}</span>
          </div>
          <div className="status-line">
            <span className="status-label">TIME:</span>
            <span className="status-value">{new Date().toLocaleTimeString()}</span>
          </div>
        </div>

        <div className="error-message">
          <p>The page you're looking for has drifted into the void of cyberspace.</p>
          <p>Fortunately, our navigation systems have several recovery options:</p>
        </div>

        <div className="navigation-suggestions">
          <div className="suggestions-header">
            <span className="terminal-prompt">swapnil@portfolio:~$</span>
            <span className="cursor-blink">_</span>
          </div>

          <div className="suggestions-list">
            {suggestions.map((suggestion, index) => (
              <div
                key={index}
                className="suggestion-item"
                onClick={suggestion.action}
                style={{ '--delay': `${index * 0.1}s` }}
              >
                <span className="suggestion-command">{suggestion.command}</span>
                <span className="suggestion-description">{suggestion.description}</span>
              </div>
            ))}
          </div>
        </div>

        <div className="help-text">
          <p>Press <kbd>Ctrl</kbd> + <kbd>`</kbd> to open terminal interface</p>
          <p>Or click any navigation option above to continue your mission</p>
        </div>
      </div>

      {/* Decorative Elements */}
      <div className="decorative-elements">
        <div className="orbit orbit-1"></div>
        <div className="orbit orbit-2"></div>
        <div className="orbit orbit-3"></div>
        <div className="satellite satellite-1"></div>
        <div className="satellite satellite-2"></div>
      </div>
    </div>
  );
};

export default NotFound;