import React, { useState } from "react";
import { Container, Row, Col, Form, Button, Alert } from "react-bootstrap";
import { FaEnvelope, FaLinkedin, FaGithub, FaMapMarkerAlt, FaPhone } from "react-icons/fa";
import * as Constants from "../../Constants/Constants";
import { trackContactAttempt } from '../../utils/analytics';
import "./Contact.css";

const Contact = () => {
  const [formData, setFormData] = useState({
    name: "",
    email: "",
    subject: "",
    message: "",
  });

  const [formSubmitted, setFormSubmitted] = useState(false);
  const [submitError, setSubmitError] = useState(null);
  const [validated, setValidated] = useState(false);
  const [isSubmitting, setIsSubmitting] = useState(false);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    const form = e.currentTarget;

    if (form.checkValidity() === false) {
      e.stopPropagation();
      setValidated(true);
      return;
    }

    // Check rate limiting (5 minutes) - skip in dev
    const isDev = process.env.REACT_APP_IS_DEV === 'true';
    if (!isDev) {
      const lastSubmit = localStorage.getItem('lastContactSubmit');
      const now = Date.now();
      if (lastSubmit && (now - parseInt(lastSubmit)) < 300000) {
        const timeLeft = Math.ceil((300000 - (now - parseInt(lastSubmit))) / 60000);
        setSubmitError(`Please wait ${timeLeft} minute(s) before submitting again.`);
        return;
      }
    }

    setIsSubmitting(true);
    setSubmitError(null);

    try {
      // Create form data for Google Apps Script
      const formDataToSend = new FormData();
      formDataToSend.append('name', formData.name);
      formDataToSend.append('email', formData.email);
      formDataToSend.append('subject', formData.subject);
      formDataToSend.append('message', formData.message);
      
      // Submit to Google Sheets using FormData instead of JSON
      const response = await fetch(process.env.REACT_APP_GOOGLE_SHEETS_URL, {
        method: 'POST',
        body: formDataToSend,
      });

      // Check if request was successful
      if (response.ok || response.status === 0) {
        // Track successful submission
        trackContactAttempt('form_submission');
        
        // Store timestamp for rate limiting
        const now = Date.now();
        localStorage.setItem('lastContactSubmit', now.toString());
        
        setFormSubmitted(true);
        setFormData({
          name: "",
          email: "",
          subject: "",
          message: "",
        });
        setValidated(false);
        
        // Scroll to top of the form to show success message
        window.scrollTo({ top: 0, behavior: "smooth" });
      } else {
        throw new Error(`HTTP ${response.status}: ${response.statusText}`);
      }
      
    } catch (error) {
      console.error('Form submission error:', error);
      setSubmitError('Failed to send message. Please try again or contact me directly.');
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <div className="contact-page">
      <div className="page-header">
        <Container>
          <h1>Get In Touch</h1>
          <p>
            I'm always open to discussing new projects, ideas, and opportunities.
            Feel free to reach out!
          </p>
        </Container>
      </div>

      <Container className="contact-container">
        <Row>
          <Col lg={5} md={5}>
            <div className="contact-info">
              <h2>Contact Information</h2>
              <p>
                Whether you have a question, a potential collaboration opportunity, 
                or just want to connect, I'd love to hear from you. Here's how you 
                can reach me:
              </p>

              <div className="contact-details">
                <div className="contact-item">
                  <div className="contact-icon">
                    <FaEnvelope />
                  </div>
                  <div className="contact-text">
                    <h5>Email</h5>
                    <a href={`mailto:${Constants.MY_EMAIL}`}>{Constants.MY_EMAIL}</a>
                  </div>
                </div>

                <div className="contact-item">
                  <div className="contact-icon">
                    <FaPhone />
                  </div>
                  <div className="contact-text">
                    <h5>Phone</h5>
                    <a href="tel:+12132948993">(213) 294-8993</a>
                  </div>
                </div>

                <div className="contact-item">
                  <div className="contact-icon">
                    <FaLinkedin />
                  </div>
                  <div className="contact-text">
                    <h5>LinkedIn</h5>
                    <a href={Constants.LINKED_IN_PROFILE} target="_blank" rel="noopener noreferrer">
                      Connect on LinkedIn
                    </a>
                  </div>
                </div>

                <div className="contact-item">
                  <div className="contact-icon">
                    <FaGithub />
                  </div>
                  <div className="contact-text">
                    <h5>GitHub</h5>
                    <a href={Constants.GITHUB_PROFILE} target="_blank" rel="noopener noreferrer">
                      View GitHub Profile
                    </a>
                  </div>
                </div>

                <div className="contact-item">
                  <div className="contact-icon">
                    <FaMapMarkerAlt />
                  </div>
                  <div className="contact-text">
                    <h5>Location</h5>
                    <p>San Jose, California</p>
                  </div>
                </div>
              </div>

              <div className="availability-section">
                <h3>Current Availability</h3>
                <p>
                  I'm currently available for:
                </p>
                <ul>
                  <li>Full-time engineering management positions</li>
                  <li>Technical consulting projects</li>
                  <li>Speaking engagements on software quality and engineering leadership</li>
                  <li>Mentoring opportunities</li>
                </ul>
              </div>
            </div>
          </Col>

          <Col lg={7} md={7}>
            <div className="contact-form">
              <h2>Send a Message</h2>
              
              {formSubmitted && (
                <Alert variant="success" className="mb-4">
                  Thank you for your message! I'll get back to you soon.
                </Alert>
              )}
              
              {submitError && (
                <Alert variant="danger" className="mb-4">
                  {submitError}
                </Alert>
              )}
              
              <Form noValidate validated={validated} onSubmit={handleSubmit}>
                <Row>
                  <Col md={6}>
                    <Form.Group className="mb-3">
                      <Form.Label>Your Name</Form.Label>
                      <Form.Control
                        type="text"
                        name="name"
                        value={formData.name}
                        onChange={handleChange}
                        required
                        placeholder="Enter your name"
                      />
                      <Form.Control.Feedback type="invalid">
                        Please enter your name.
                      </Form.Control.Feedback>
                    </Form.Group>
                  </Col>
                  
                  <Col md={6}>
                    <Form.Group className="mb-3">
                      <Form.Label>Email Address</Form.Label>
                      <Form.Control
                        type="email"
                        name="email"
                        value={formData.email}
                        onChange={handleChange}
                        required
                        placeholder="Enter your email"
                      />
                      <Form.Control.Feedback type="invalid">
                        Please enter a valid email address.
                      </Form.Control.Feedback>
                    </Form.Group>
                  </Col>
                </Row>
                
                <Form.Group className="mb-3">
                  <Form.Label>Subject</Form.Label>
                  <Form.Control
                    type="text"
                    name="subject"
                    value={formData.subject}
                    onChange={handleChange}
                    required
                    placeholder="Enter subject"
                  />
                  <Form.Control.Feedback type="invalid">
                    Please enter a subject.
                  </Form.Control.Feedback>
                </Form.Group>
                
                <Form.Group className="mb-4">
                  <Form.Label>Message</Form.Label>
                  <Form.Control
                    as="textarea"
                    rows={6}
                    name="message"
                    value={formData.message}
                    onChange={handleChange}
                    required
                    placeholder="Enter your message"
                  />
                  <Form.Control.Feedback type="invalid">
                    Please enter your message.
                  </Form.Control.Feedback>
                </Form.Group>
                
                <Button type="submit" className="submit-button" disabled={isSubmitting}>
                  {isSubmitting ? 'Sending...' : 'Send Message'}
                </Button>
              </Form>
            </div>
          </Col>
        </Row>
      </Container>

      <div className="map-section">
        <iframe
          src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d101354.13276254104!2d-121.9756853072754!3d37.339385199999995!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x808fcca1767e346f%3A0xd11de7d5153c4420!2sSan%20Jose%2C%20CA%2095134!5e0!3m2!1sen!2sus!4v1713553600045!5m2!1sen!2sus"
          width="100%"
          height="450"
          style={{ border: 0 }}
          allowFullScreen=""
          loading="lazy"
          referrerPolicy="no-referrer-when-downgrade"
          title="San Jose Map"
        ></iframe>
      </div>
    </div>
  );
};

export default Contact;
