class Particle {
  constructor({ x, y, mass = 1, type = 'planet', canvas, useRealisticMass = false }) {
    this.x = x;
    this.y = y;
    this.vx = 0;
    this.vy = 0;
    this.ax = 0;
    this.ay = 0;
    this.type = type;
    this.canvas = canvas;
    this.useRealisticMass = useRealisticMass;
    
    if (useRealisticMass) {
      this.mass = this.getRealisticMassValue();
    } else {
      this.mass = Math.max(0.01, mass);
    }
    
    this.size = Math.max(2, Math.min(12, Math.sqrt(Math.abs(this.mass)) * 1.5));
    this.color = this.getColorByType();
    this.massColor = this.getMassBasedColor();
    
    this.trail = [];
    this.maxTrailLength = 30;
    this.orbitalTrail = [];
    this.maxOrbitalTrailLength = 200;
    
    this.G = 0.001;
  }

  getRealisticMassValue() {
    const masses = {
      sun: 333000,
      mercury: 0.055,
      venus: 0.815,
      earth: 1.0,
      mars: 0.107,
      jupiter: 317.8,
      saturn: 95.2,
      uranus: 14.5,
      neptune: 17.1,
      pluto: 0.002,
      star: 100,
      planet: 1,
      comet: 0.001,
      object: 0.1
    };
    return masses[this.type] || 1;
  }

  getMassBasedColor() {
    const mass = this.mass;
    
    if (mass <= 5) {
      return '#8B4513';
    } else if (mass <= 15) {
      return '#CD853F';
    } else if (mass <= 35) {
      return '#FFD700';
    } else if (mass <= 60) {
      return '#FFA500';  
    } else if (mass <= 85) {
      return '#FF6347';
    } else {
      return '#DC143C';
    }
  }

  getColorByType() {
    const colors = {
      planet: '#4A90E2',
      star: '#FFD700', 
      comet: '#87CEEB',
      object: '#CD853F',
      sun: '#FFA500',
      mercury: '#8C7853',
      venus: '#FFC649',
      earth: '#6B93D6',
      mars: '#CD5C5C',
      jupiter: '#D8CA9D',
      saturn: '#FAD5A5',
      uranus: '#4FD0E7',
      neptune: '#4B70DD',
      pluto: '#8A7853'
    };
    return colors[this.type] || colors.planet;
  }


  calculateGravitationalForce(other) {
    const dx = other.x - this.x;
    const dy = other.y - this.y;
    const distanceSquared = dx * dx + dy * dy;
    const distance = Math.sqrt(distanceSquared);
    
    if (distance < this.getCollisionRadius(other)) {
      return { fx: 0, fy: 0, collision: true };
    }
    
    const force = this.G * this.mass * other.mass / distanceSquared;
    const fx = force * dx / distance;
    const fy = force * dy / distance;
    
    return { fx, fy, collision: false };
  }

  getCollisionRadius(other) {
    const thisRadius = this.getPhysicalRadius();
    const otherRadius = other.getPhysicalRadius();
    return thisRadius + otherRadius;
  }

  getPhysicalRadius() {
    if (this.type === 'sun') return 15;
    if (this.type === 'jupiter' || this.type === 'saturn') return 8;
    if (this.type === 'uranus' || this.type === 'neptune') return 6;
    if (this.type === 'earth' || this.type === 'venus') return 4;
    if (this.type === 'mars' || this.type === 'mercury') return 3;
    if (this.type === 'pluto' || this.type === 'comet') return 2;
    return this.size;
  }

  getEscapeVelocity(distance) {
    return Math.sqrt(2 * this.G * this.mass / distance);
  }

  shouldBeDestroyed(other) {
    if (this.type === 'sun' || other.type === 'sun') return false;
    
    const dx = other.x - this.x;
    const dy = other.y - this.y;
    const distance = Math.sqrt(dx * dx + dy * dy);
    
    const isStellar = ['sun', 'star'].includes(other.type);
    const stellarDestructionRadius = isStellar ? other.getPhysicalRadius() * 2 : 0;
    
    return distance < stellarDestructionRadius && other.mass > this.mass * 100;
  }

  applyForce(fx, fy) {
    this.ax += fx / this.mass;
    this.ay += fy / this.mass;
  }

  update(timeScale = 1, dt = 0.016, showOrbitalPaths = false) {
    const scaledDt = dt * timeScale;
    
    this.x += this.vx * scaledDt + 0.5 * this.ax * scaledDt * scaledDt;
    this.y += this.vy * scaledDt + 0.5 * this.ay * scaledDt * scaledDt;
    
    this.vx += this.ax * scaledDt;
    this.vy += this.ay * scaledDt;
    
    if (this.x < 0) this.x = this.canvas.width;
    if (this.x > this.canvas.width) this.x = 0;
    if (this.y < 0) this.y = this.canvas.height;
    if (this.y > this.canvas.height) this.y = 0;
    
    this.ax = 0;
    this.ay = 0;
    
    this.updateTrail(showOrbitalPaths);
  }

  updateTrail(showOrbitalPaths = false) {
    this.trail.push({ x: this.x, y: this.y, timestamp: Date.now() });
    
    if (this.trail.length > this.maxTrailLength) {
      this.trail.shift();
    }
    
    if (showOrbitalPaths) {
      this.orbitalTrail.push({ x: this.x, y: this.y });
      if (this.orbitalTrail.length > this.maxOrbitalTrailLength) {
        this.orbitalTrail.shift();
      }
    } else {
      this.orbitalTrail = [];
    }
  }

  render(ctx, distanceScale = 10, showOrbitalPaths = false) {
    this.renderTrail(ctx, distanceScale);
    if (showOrbitalPaths) this.renderOrbitalPath(ctx);
    
    ctx.save();
    
    const renderColor = this.useRealisticMass ? this.color : this.massColor;
    ctx.fillStyle = renderColor;
    
    ctx.shadowBlur = Math.max(5, this.size);
    ctx.shadowColor = renderColor;
    
    ctx.beginPath();
    ctx.arc(this.x, this.y, this.size, 0, Math.PI * 2);
    ctx.fill();
    ctx.restore();
  }

  renderOrbitalPath(ctx) {
    if (this.orbitalTrail.length < 2) return;
    
    ctx.save();
    ctx.globalAlpha = 0.3;
    ctx.strokeStyle = this.color;
    ctx.lineWidth = 1;
    
    ctx.beginPath();
    ctx.moveTo(this.orbitalTrail[0].x, this.orbitalTrail[0].y);
    
    for (let i = 1; i < this.orbitalTrail.length; i++) {
      ctx.lineTo(this.orbitalTrail[i].x, this.orbitalTrail[i].y);
    }
    
    ctx.stroke();
    ctx.restore();
  }

  renderTrail(ctx, distanceScale = 10) {
    if (this.trail.length < 2) return;
    
    ctx.save();
    for (let i = 1; i < this.trail.length; i++) {
      const current = this.trail[i];
      const previous = this.trail[i - 1];
      const alpha = i / this.trail.length * 0.5;
      
      ctx.globalAlpha = alpha;
      ctx.strokeStyle = this.color;
      ctx.lineWidth = (this.size / 2) * alpha * (distanceScale / 10);
      
      ctx.beginPath();
      ctx.moveTo(previous.x, previous.y);
      ctx.lineTo(current.x, current.y);
      ctx.stroke();
    }
    ctx.restore();
  }

  getDetailedInfo(sunPosition = null) {
    const velocity = Math.sqrt(this.vx * this.vx + this.vy * this.vy);
    const acceleration = Math.sqrt(this.ax * this.ax + this.ay * this.ay);
    
    let distanceFromSun = null;
    let orbitalInfo = {};
    
    if (sunPosition && this.type !== 'sun') {
      const dx = this.x - sunPosition.x;
      const dy = this.y - sunPosition.y;
      distanceFromSun = Math.sqrt(dx * dx + dy * dy);
      
      const auDistance = distanceFromSun / 25;
      orbitalInfo = {
        distanceFromSun: auDistance.toFixed(2) + ' AU',
        orbitalVelocity: velocity.toFixed(3) + ' px/frame'
      };
    }
    
    return {
      name: this.type.charAt(0).toUpperCase() + this.type.slice(1),
      mass: this.mass.toFixed(3) + ' Earth masses',
      velocity: velocity.toFixed(3) + ' px/frame',
      acceleration: acceleration.toFixed(6) + ' px/frame²',
      position: `(${this.x.toFixed(1)}, ${this.y.toFixed(1)})`,
      physicalRadius: this.getPhysicalRadius() + ' px',
      ...orbitalInfo
    };
  }
}

export default Particle;